          <div class="text">2020-04-01</div>
        </div>
      </van-panel>
    </div>
  </van-pull-refresh>
</template>

<script>
  export default {
    name: "NotAccepted",
    data() {
      return {
        isLoading: false,
      }
    },
    created() {

    },
    methods: {
      onRefresh () {
        this.isLoading = false
      },
      toEdit (id) {
        console.log(id)
        this.$router.push({
          name: 'addInfo',
